.blob {
    position: absolute;
	top: 0;
    left: 0;
	fill: #ED64A6;
    width: 6rem;
    z-index: -1;
    animation: move 10s ease-in-out infinite;
    transform-origin: 50% 50%;
}

.blob2 {
	position: absolute;
	top: 0;
	left: 0;
	fill: #00A2EB;
	width: 6rem;
	z-index: -1;
	animation: move2 6s ease-in-out infinite;
	transform-origin: 50% 50%;
}

.blob3 {
	position: absolute;
	top: 0;
	left: 0;
	fill: #8000D7;
	width: 6rem;
	z-index: -1;
	animation: move3 8s ease-in-out infinite;
	transform-origin: 50% 50%;
}
  
@keyframes move {
    0%   { transform: scale(1)   translate(10px, -30px); }
    38%  { transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    40%  { transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    78%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    80%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    100% { transform: scale(1)   translate(10px, -30px); }
}

@keyframes move2 {
	0%   { transform: scale(1)   translate(10px, -70px); }
	38%  { transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
	40%  { transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
	78%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
	80%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
	100% { transform: scale(1)   translate(10px, -70px); }
}

@keyframes move3 {
	0%   { transform: scale(1)   translate(0vw, 0vh); }
	38%  { transform: scale(0.8, 1) translate(80vw, 0vh) rotate(160deg); }
	40%  { transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
	78%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
	80%  { transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
	100% { transform: scale(1)   translate(0vw, 0vh); }
}
